@import '../../global-styles/mixins';

.main {
    padding: 16px;
    background: linear-gradient(90deg, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 150%);
    background-attachment: local;
    
    
    &__pic {
        width: 200px;
        display: block;
        margin: 0 auto;
        clip-path: circle();


    }

    &__name {
        font-family: 'Raleway';
        font-weight: 100;
        color: white;
        text-align: center;
        filter:blur(0.4px);
        font-size: 40px;
        margin: 0;
    }

    &__tagline {
        color: white;
        font-family: 'Montserrat';
        font-weight: 100;
        font-size: 16px;
        padding-bottom: 48px;
        text-align: center;

    }
}